import { Component, OnInit } from '@angular/core';
import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators
} from '@angular/forms';
import { ControlService } from './control.service';
import {
  IFormDetail,
  IControl,
  IForm
} from '../requestResponseModel/control.interface';
import { BehaviorSubject, Observable, Subject, of } from 'rxjs';
import { ReCaptchaV3Service } from 'ngx-captcha';
import { AuthService } from '../services/auth.service';
import { ISignUp } from '../requestResponseModel/request.interface';
import { IResponse } from '../requestResponseModel/response.interface';
import { LoaderService } from 'src/app/shared/loader/loader.service';
import { debounceTime, distinctUntilChanged, switchMap, map, flatMap, delay } from 'rxjs/operators';
import { NotifyService } from 'src/app/shared/notify/notify.service';
import { Router } from '@angular/router';
import { CustomValidators } from '../validators/custom.validaors';
import { UtilityService } from 'src/app/shared/services/platwareService/utility.service';
import { AppConstant } from 'src/app/shared/services/AppConstant';

declare const $: any;

@Component({
  selector: 'app-sign-up',
  templateUrl: './sign-up.component.html',
  styleUrls: ['./sign-up.component.scss']
})
export class SignUpComponent implements OnInit {
  public signUpForm: FormGroup;
  public iFormCallback = new BehaviorSubject<IFormDetail>(null);
  public iForm: IFormDetail;
  private validateFields = new Subject<string>();
  private activeControl: any;
  private emailErrorMessage: String;
  public keyUp = new Subject<KeyboardEvent>();

  constructor(
    private formBuilder: FormBuilder,
    public controlService: ControlService,
    private reCaptchaV3Service: ReCaptchaV3Service,
    private authService: AuthService,
    private loaderService: LoaderService,
    private notifyService: NotifyService,
    private router: Router,
    private utilityService: UtilityService
  ) {
    this.getFormDataAPI();
    this.subscribeKeyUp();

  }

  private addRecapture() {
    if (this.iForm.isCaptchaRequired) {
      // this.reCaptchaV3Service.execute('6LeIxAcTAAAAAJcZVRqyHh71UMIEGNQ_MXjiZKhI', 'homepage', (token) => {
      //   console.log('This is your token: ', token);
      // }, {
      //     useGlobalDomain: false
      // });
    }
  }

  public handleSuccess(event) {
    console.log(event);
  }
  public click(event) {
    console.log(this.signUpForm);
  }

  private addContols() {
    const contols = this.iForm.controls;
    for (let i = 0; i < contols.length; i++) {
      this.signUpForm.addControl(
        contols[i].id.toString(),
        this.controlService.getFormControl(contols[i])
      );
    }
    // this.signUpForm.addControl(
    //   "TermsCondition",
    //   new FormControl("", [Validators.required, Validators.pattern("true")])
    // );
    if (this.iForm.isCaptchaRequired) {
      //  this.signUpForm.addControl('recaptcha', new FormControl('', [Validators.required]));
    }
    this.signUpForm.setValidators(CustomValidators.passwordMatcher);
  }

  public getFormData(): Observable<IFormDetail> {
    return this.iFormCallback.asObservable();
  }

  ngOnInit() {

    setTimeout(() => {
      $(document).ready(function() {
        $('.tooltipped').tooltip();
      });

    }, 500)


    this.signUpForm = this.formBuilder.group({});
    this.initValidateFields();
  }

  public getFormGroup() { }

  /*This method takes sign up form value from html */
  public signUp() {

    console.log(this.signUpForm.value as ISignUp);

    this.loaderService.showLoader();
    this.authService.signUp(this.signUpForm.value as ISignUp).subscribe(
      (resp: IResponse) => {
        console.log(resp);
        if (resp) {
          this.loaderService.hideLoader();
          this.notifyService.showToast('successfully registered', 'success');
          let email = this.signUpForm.value.email;
          this.utilityService.setLocalStorage(AppConstant.SIGNUP_EMAIL, email);
          this.router.navigate(['auth/login']);
        }
      },
      err => {
        //HERE
        this.utilityService.getError(err, 'Create_User');
        this.loaderService.hideLoader();
        console.log(err);
        err = err[Object.keys(err)[0]];

      }
    );
  }

  /*End of This method takes sign up form value from html */

  public initValidateFields() {
    this.validateFields
      .pipe(
        debounceTime(500),
        distinctUntilChanged()
      )
      .subscribe(searchText => {
        this.validateSearch(searchText);
      });
  }

  /* This method performs the API Call for search  */
  public validateSearch(value): Observable<any> {
    this.authService
      .validateFields(value, this.activeControl.controlType)
      .subscribe(
        resp => {
          console.log(resp);
        },
        err => {
          //HERE
          console.log(err);
          this.utilityService.getError(err, 'VAHANA_VALIDATE_FIELDS');
          err = err[Object.keys(err)[0]];
          this.emailErrorMessage = err.message;
          this.signUpForm.controls[this.activeControl.id].setErrors({
            duplicate: true
          });
        }
      );
    return of('');
  }

  /* This method set the value passed from html for fileds for valodation */
  checiIfExist(textValue: string, control: any) {
    this.signUpForm.controls[control.id].valueChanges
      .pipe(
        debounceTime(400),
        distinctUntilChanged()
      )
      .subscribe(value => {
        if (control.isAPIToCheck) {
          this.activeControl = control;
          this.validateFields.next(value);
        }
      });
  }

  public navigateToLogin(): void {
    this.router.navigate(['auth/login']);
  }

  public navigateToVahana(): void {
    this.router.navigate(['auth/login']);
  }

  /* Get form json Data */
  public getFormDataAPI(): void {
    this.loaderService.showLoader();
    this.controlService.getFormAPIData().subscribe(
      value => {
        if (value) {
          this.iFormCallback.next(value.signUpForm);
          this.iForm = value.signUpForm;
          this.addContols();
          this.addRecapture();
          this.loaderService.hideLoader();
        }
      },
      err => {
        this.loaderService.hideLoader();
      }
    );
  }

  public subscribeKeyUp(): void {
    this.keyUp.pipe(
      debounceTime(300),
      distinctUntilChanged(),
      flatMap(data => of(data).pipe(delay(0)))
    ).subscribe((data: any) => {
      if (data.control.isAPIToCheck && this.signUpForm.controls[data.control.id].valid) {
        this.activeControl = data.control;
        this.validateSearch(data.value);
      }
    });
  }


}
