import { Injectable } from '@angular/core';
import { PlatwareService } from 'src/app/shared/services/platwareService/platware.service';
import { Observable } from 'rxjs';
import * as env from '../../../environments/environment';
import { UtilityService } from 'src/app/shared/services/platwareService/utility.service';

@Injectable()
export class OTPService {

    constructor(private platwareService: PlatwareService, private utilityService: UtilityService) {}

    public generateOTP(mobileNumber: string): Observable<any>{
        const reqBody = {
          GETOTP: [{
            serviceType: 'LOGIN',
            reference: '123',
            appId: env.environment.envProps['appId'],
            orgId: env.environment.envProps['orgId'],
            timestamp:  this.utilityService.getDateInISOFormat(),
            mobile: mobileNumber
          }]
        };
        console.log(reqBody);
        const reqHeader = {
          custom_headers: JSON.stringify({
            'Content-type': 'application/json'
          })
        };
        return this.platwareService.callPlatware(reqBody, reqHeader);
       }


       /* API Call to Validate OTP Entered By User */
    public verfiyOTP(enteredOTP: string,refId: string): Observable<any> {
        const reqBody = {
            VERIFYOTP: [{
              serviceType: 'LOGIN',
              reference: refId,
              timestamp:  this.utilityService.getDateInISOFormat(),
              appId: env.environment.envProps['appId'],
              orgId: env.environment.envProps['orgId'],
              otp: enteredOTP
            }]
          };
        const reqHeader = {
            custom_headers: JSON.stringify({
              'Content-type': 'application/json'
            })
          };
        return this.platwareService.callPlatware(reqBody, reqHeader);
    }
    /* End of API Call to Validate OTP Entered By User */
}