import { Component, OnInit, Input, ElementRef, OnDestroy, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { OTPService } from '../services/otp.service';
import { NotifyService } from 'src/app/shared/notify/notify.service';
import { Router } from '@angular/router';
import { BehaviorSubject } from 'rxjs';
import { UtilityService } from 'src/app/shared/services/platwareService/utility.service';


@Component({
  selector: 'app-verify-otp',
  templateUrl: './verify-otp.component.html',
  styleUrls: ['./verify-otp.component.scss']
})
export class VerifyOTPComponent implements OnInit, OnDestroy {
  @Input() groupName: FormGroup;
  @Input() isSetNewPassword: BehaviorSubject<boolean>;
  @Output() optFilled: EventEmitter<boolean>;
  public otpArrayClassName: Array<string> = [];
  public otpLength = 6;
  private mobileNumber: string;
  private referenceId: string;
  public isOTPLoader: boolean;
  constructor(private element: ElementRef, private otpService: OTPService,
              private notifyService: NotifyService, private router: Router,
              private utilityService: UtilityService) {
      this.optFilled = new EventEmitter();
     }

  ngOnInit() {
    this.generateOTP();
    this.initIsSetNewPassword();
    this.groupName.setErrors(null);
  }


  /* Initialize OTP Form */
  public initOTPForm(): void {
    for (let i = 0; i < this.otpLength; i++) {
      this.groupName.addControl('digit' + i, new FormControl('', Validators.required));
      this.otpArrayClassName.push('digit' + i);
    }
  }

  /* End of Initialization form */


  /* set Focus Back */
  private setFocusBack(className: string, index: string) {
    if ((+index) === (0)) {
      return;
    }
    const nextIndex = +(index) - 1;
    className = className.substring(0, className.length - 1) + nextIndex;
    const divToScroll = this.element.nativeElement.querySelector('.' + className);
    setTimeout(() => {
      divToScroll.focus();
    }, 0);
  }


  public setFocus(className: string, index: string) {
    if (this.combineOTP().length === this.otpLength) {
      this.optFilled.emit(true);
    } else {
      this.optFilled.emit(false);
    }
    if ((+index) === this.otpArrayClassName.length - 1) {
      return;
    }
    const nextIndex = +(index) + 1;
    className = className.substring(0, className.length - 1) + nextIndex;
    const divToScroll = this.element.nativeElement.querySelector('.' + className);
    setTimeout(() => {
      divToScroll.focus();
    }, 0);
  }

  /* End of set focous of Input forward */


  public combineOTP(): string {
    let otp = '';
    const enteredOTPFormObj = Object.assign(this.groupName.value);
    delete enteredOTPFormObj.userInput;
    Object.keys(this.groupName.value).forEach((value) => {
      otp += this.groupName.value[value];
    });
    return otp;
  }

  public next(event, className: string, index: string): void {
    if (event.code === 'Backspace' || event.keyCode === 8) {
      event.preventDefault();
      this.setFocusBack(className, index);
      return;
    } else {
      this.setFocus(className, index);
    }
  }


  public generateOTP(isRequestAgain: boolean = false): void {
    // userInput = mobileNumber
    this.isOTPLoader = true;
    this.otpService.generateOTP(this.groupName.value.userInput).subscribe((response) => {
      if (response) {
        response = response[Object.keys(response)[0]];
        if (!isRequestAgain) { this.initOTPForm(); } else { this.resetOTPFormControl(); }
        this.referenceId = response.referance;
        this.isOTPLoader = false;
      }
    },
      error => {
        // HERE
        console.log(error);
       
        error = error[Object.keys(error)[0]];
        this.isOTPLoader = false;
        this.utilityService.getError(error,'GETOTP')
        this.isSetNewPassword.next(false);
      });
  }


  /* This method validates the OTP */
  private validateOTP() {
    if (this.combineOTP().length === this.otpLength) {
      this.otpService.verfiyOTP(this.combineOTP(), this.referenceId).subscribe((response) => {
        if (response) {
          response = response[Object.keys(response)[0]];
          this.notifyService.showToast(response.message, 'success');
          this.navigateToNewPassword();
        }
      },
        error => {
          // HERE
          console.log(error);

          error = error[Object.keys(error)[0]];
          // this.isSetNewPassword.next(false);
          this.utilityService.getError(error,'VERIFYOTP');
        });
    } else {
      this.notifyService.showToast('Please enter OTP', 'error');
    }
  }
  /* End of This method validates the OTP */

  public navigateToNewPassword() {
    this.router.navigate(['auth/newPassword']);
  }

  public initIsSetNewPassword() {
    this.isSetNewPassword.subscribe((flag: boolean) => {
      if (flag) {
        this.validateOTP();
      } else { return; }
    });
  }


  public ngOnDestroy(): void {
    this.removeOTPFormControl();
  }

  public removeOTPFormControl(): void {
    for (let i = 0; i < this.otpLength; i++) {
      this.groupName.removeControl('digit' + i);
    }
    this.otpArrayClassName = [];
  }

  public resetOTPFormControl(): void {
    for (let i = 0; i < this.otpLength; i++) {
      this.groupName.controls['digit' + i].reset();
    }
  }

  public navigateToVahana(): void {
    this.router.navigate(['auth/login']);
  }


}
