import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { AuthService } from '../services/auth.service';
import { CustomValidators } from '../validators/custom.validaors';
import { LoaderService } from 'src/app/shared/loader/loader.service';
import { NotifyService } from 'src/app/shared/notify/notify.service';
import { UtilityService } from 'src/app/shared/services/platwareService/utility.service';

@Component({
  selector: 'app-new-password',
  templateUrl: './new-password.component.html',
  styleUrls: ['./new-password.component.scss']
})
export class NewPasswordComponent implements OnInit {
  public newPasswordForm: FormGroup;

  constructor(private formBuilder: FormBuilder, private router: Router,
    private authService: AuthService, private loaderService: LoaderService,
    private notifyService: NotifyService, private utilityService: UtilityService) { }

  ngOnInit() {
    this.initForm();
  }
  public initForm() {
    this.newPasswordForm = this.formBuilder.group({
      password: ['', [Validators.required, Validators.pattern('^(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{9,16}$')]],
      confirmPassword: ['', [Validators.required]]
    }, { validators: CustomValidators.passwordMatcher });
  }
  submit() {
    this.loaderService.showLoader();
    debugger
    this.authService.resetPassword(this.newPasswordForm.value.password).subscribe((resp) => {
      if (resp) {
        this.loaderService.hideLoader();
        this.notifyService.showToast('Password reset successfully', 'success');
        this.router.navigate(['auth/login']);
      }
    }, err => {
      //HERE
      this.utilityService.getError(err,'VAHANA_RESETPASSWORD');

      err = err[Object.keys(err)[0]];
      this.loaderService.hideLoader();
      
    });

  }

  public navigateToSignUp(): void {
    this.router.navigate(['auth/signUp']);
  }

  public navigateToVahana(): void {
    this.router.navigate(['auth/login']);
  }
}
