import { Injectable } from '@angular/core';
import { PlatwareService } from 'src/app/shared/services/platwareService/platware.service';
import { ISignUp, IValidateFields } from '../requestResponseModel/request.interface';
import { Observable } from 'rxjs';
import { IResponse } from '../requestResponseModel/response.interface';
import { ValueConverter } from '@angular/compiler/src/render3/view/template';
import * as environment from '../../../environments/environment';
import * as PlatwareClient from '../../../../PlatwareClient';
import { HttpClient } from '@angular/common/http';
@Injectable()
export class AuthService {

   public forgotUserInput: object = {};
    constructor(private platwareService: PlatwareService, private http: HttpClient) {}

    /* Method for SignUp Api Call */
      public signUp(signUpObject: ISignUp): Observable<IResponse> {
        const body = {Create_User: [signUpObject]};
        const header = {
            custom_headers: JSON.stringify({
              'Content-type': 'application/json'
            })
          };
        return this.platwareService.callPlatware(body, header);
      }

    /* End of Method for SignUp Api Call */


    /* Method for SignIn */
    public signIn(signInObject, callBack): void {
      const body = {AUTH_VAHANA: [signInObject]};
      const header = {
          custom_headers: JSON.stringify({
            'Content-type': 'application/json'
          })
        };

      PlatwareClient.executeApi(environment, body, header, callBack);
      // return this.platwareService.callPlatware(body, header);
    }


    /* End of Method for SignIn */

   /* Method for Checking the userExist or Not */
   public validateFields(value: string, type: string): Observable<any> {

    const body = {VAHANA_VALIDATE_FIELDS: [{}]};
    const header = {
        custom_headers: JSON.stringify({
          'Content-type': 'application/json',
          type: type,
          value: value
        })
      };
    console.log(body, header);
    return this.platwareService.callPlatware(body, header);
   }

  /* Vahana reset Password */
  public resetPassword(password: any): Observable<any> {
    const resetReq = this.forgotUserInput;
    resetReq['password'] = password;
    console.log(resetReq);
    const body = {VAHANA_RESETPASSWORD: [resetReq]};
    const header = {
        custom_headers: JSON.stringify({
          'Content-type': 'application/json'
        })
      };
    return this.platwareService.callPlatware(body, header);
  }
   /* End of Vahana reset Password */


   public getCaptureKey(): Observable<any> {
    return this.http.get('/assets/login/data/additionalData.json');
   }
}
