import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { AuthService } from '../services/auth.service';
import { BehaviorSubject, Subject, Observable, of } from 'rxjs';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';
import { NotifyService } from 'src/app/shared/notify/notify.service';

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.scss']
})
export class ForgotPasswordComponent implements OnInit {
  public forgotPasswordForm: FormGroup;
  private validateFields = new Subject<object>();
  private mobileNumberRegex: any = new RegExp(/^[789]\d{9}$/);
  private emailRegex: any = new RegExp('^[a-z0-9]+(\\.[_a-z0-9]+)*@[a-z0-9-]+(\\.[a-z0-9-]+)*(\\.[a-z]{2,15})$');
  public isOTPScreen: boolean;
  public isSetNewPassword: BehaviorSubject<boolean> = new BehaviorSubject(false);
  public isValidateAPICompleted: Subject<boolean>;
  public showSmallLoader: boolean;
  public enableField=false;
  constructor(private formBuilder: FormBuilder, private router: Router, private authService: AuthService,
    private notifyService: NotifyService) { }

  ngOnInit() {
    this.initForm();
    this.validateUserInput();
    this.initValidateFields();
    this.subScribeISSetNewPassword();

  }
  public initForm() {
    this.forgotPasswordForm = this.formBuilder.group({
      userInput: ['', [Validators.required]],
    });
  }
  submit() {
    this.authService.forgotUserInput = this.getEmailORMobileNumberObject(this.forgotPasswordForm.value.userInput);
    this.navigateToNewPassword();

  }

  public resendOTP() {
    console.log('resend OTP');
  }

  public navigateToNewPassword() {
    if (this.isOTPScreen) {
      this.isSetNewPassword.next(true);
    } else {
      this.isSetNewPassword.next(false);
      this.router.navigate(['auth/newPassword']);
    }

  }

  /* Get Email Or Mobile Number Object for User Exist or not */
  public getEmailORMobileNumberCodeObj(value: string): object {
    let reqObj = {};
    const isMobile = this.mobileNumberRegex.test(value);
    if (isMobile) {
      //1 is code for mobile
      reqObj = { 1: value };
    } else {
      //2 is code for email
      reqObj = { 2: value }
    }
    return reqObj;
  }
  /* End of Get Email Or Mobile Number Object */




  public getEmailORMobileNumberObject(value: string): object {

    let reqObj = {};
    const isMobile = this.mobileNumberRegex.test(value);
    if (isMobile) {
      reqObj = { mobileNumber: value };
    } else {
      reqObj = { email: value }
    }
    return reqObj;
  }

  public validateUserInput() {
    this.forgotPasswordForm.controls['userInput'].valueChanges.pipe(debounceTime(400), distinctUntilChanged()).subscribe((inputValue) => {
      if (this.mobileNumberRegex.test(inputValue)) {
        const userInput = this.getEmailORMobileNumberCodeObj(inputValue);
        this.validateFields.next(userInput);
        this.isValidateAPICompleted = new Subject();
        this.showOTPScreenOnValidateAPIResponse();
        // setting error so button remain Disabled until validation is not done yet
      } else if (this.emailRegex.test(inputValue)) {
        /* Commented for now */
        this.notifyService.showToast('Email are not allowed', 'error');
        this.forgotPasswordForm.controls['userInput'].setErrors({ 'patternError': true });
        // const userInput = this.getEmailORMobileNumberCodeObj(inputValue);
        // this.validateFields.next(userInput);
        // this.isOTPScreen = false;
      }
      else {
        this.isOTPScreen = false;
        this.forgotPasswordForm.controls['userInput'].setErrors({ 'patternError': true });
      }
    });
  }


  public initValidateFields(): void {
    this.validateFields.pipe(debounceTime(500),
      distinctUntilChanged()).subscribe((userInputObj: object) => {
        if (userInputObj) {
          this.validateAPI(userInputObj);
        }
      });
  }

  public validateAPI(userInputObj: object): Observable<any> {
    /* this api return the true if account doesn't exist */
    this.showSmallLoader = false;
    this.authService.validateFields(userInputObj[Object.keys(userInputObj)[0]], Object.keys(userInputObj)[0]).subscribe((resp) => {
      if (resp) {
        this.showSmallLoader = false;
        this.forgotPasswordForm.controls['userInput'].setErrors({ accountDoesNotExist: true });
      }
    },
      err => {
        //HERE
        this.showSmallLoader = false;
        if (err.errorCode == 'ROUTER_TARGERSERVER_EXECUTION') {
          this.forgotPasswordForm.controls['userInput'].setErrors({ errorInExecutingAPI: true });
          this.notifyService.showToast(err.errorCode, 'error');
        } else {
          this.forgotPasswordForm.controls['userInput'].setErrors(null);
          this.isValidateAPICompleted.next(true);
        }

      });
    return of('');
  }


  public showOTPScreenOnValidateAPIResponse() {
    this.isValidateAPICompleted.subscribe((isCompleted) => {
      if (isCompleted) {
        this.isOTPScreen = true;
        this.forgotPasswordForm.setErrors({ 'otpNotGenerated': true });
        this.isValidateAPICompleted.unsubscribe();
      }
    });
  }

  public subScribeISSetNewPassword() {
    console.log('init set');
    this.isSetNewPassword.subscribe((resp) => {
      if (!resp) {
        console.log('init set true');
        this.showSmallLoader = false;
        this.forgotPasswordForm.controls['userInput'].setErrors({ aacountNotExist: true });
      } else {
        console.log('init seswswswt');
        this.forgotPasswordForm.controls['userInput'].setErrors(null);
      }
    });
  }


  public navigateToLogin() {
    this.router.navigate(['auth/login']);
  }

  public navigateToVahana(): void {
    this.router.navigate(['auth/login']);
  }


  enableDisableButton(value){
    console.log("enable/disable Button: ",value);
    console.log(this.forgotPasswordForm)
    this.enableField = value;
  }
}
